    // step: 1,
    isDisabled: true,
    isLoading: false,
    uploadedFile: [],
    uploadedFileName: '',
    dropzoneOptions: {
      url: 'https://httpbin.org/post',
      acceptedFiles: '.xls, .xlsx',
      thumbnailWidth: 150,
      maxFilesize: 0.5,
      addRemoveLinks: true,
      uploadMultiple: false,
      maxFiles: 1,
    },
    tickets: [{ name: 'test' }],
    headers: ['Test header'],

    baseUrl: 'http://volchenok.com/assets/',
    currentCategory: '',
    excel: '',
  }),

  methods: {
    handleFileUpload(file) {
      this.isLoading = true
      let formData = new FormData()
      formData.append('file', file)

      this.$store.dispatch('sendFile', formData)
    },

    setStep() {
      // this.isDisabled = true
      // if (this.step === 1) {
      //   this.currentCategory = ''
      //   this.isLoading = true
      //   setTimeout(() => {
      //     ;(this.isLoading = false)((this.isDisabled = false)((this.step = 2)))
      //   }, 5000)
      // } else if (this.step === 2) {
      //   this.isLoading = true
      //   // this.fileDownload()
      //   setTimeout(
      //     () =>
      //       (this.isLoading = false)((this.isDisabled = false))(
      //         this.$parent.createTable(this.excel)
      //       ),
      //     5000
      //   )
      // }
      // this.step++

      this.$emit('next', 2)
    },
    handleUpload(file) {
      // this.$refs["excel-upload-input"].click();
      this.isDisabled = false
      this.isLoading = false
      this.uploadedFileName = file.name
    },
    fileDeleteHandler() {
      this.$store.commit('RESET')
    },
  },
}
</script>

<style lang="scss">
.dropzone {
  display: flex;
  justify-content: center;
  min-height: 90vh;
  position: relative;
}

.dz-message {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
</style>
